export function recursiveMerge(obj1, obj2) {
    for (const [key, value] of Object.entries(obj2)) {
        if (typeof value === "object" && value !== null) {
            obj1[key] = obj1[key] ? {...obj1[key]} : {};
            recursiveMerge(obj1[key], value);
        } else {
            obj1[key] = value;
        }
    }
    return obj1;
}

export default {
    recursiveMerge,
}
